
import dayjs from "dayjs";
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import Scroll from "@/components/scrollAll";
import NoData from "@/components/noData";
import Query from "@/components/filter";
export default {
data() {
return {
clientList: [],
params: {
beginFollowupDate: "",
endFollowupDate: "",
page: 1,
count: 10,
follState: 0,
custState: 0,
createUserId: this.$store.state.CurrentUserID
},
pullUp: {
threshold: 0,
txt: {
more: "加载更多",
noMore: "没有更多了"
}
},
pullDown: {
threshold: 50,
stop: 20
},
isSort: false
};
},
mounted() {
this.getData();
this.$bus.once("refrech", () => {
this.getData(1, 1);
});
const query = this.$route.query;
this.params.beginFollowupDate = query.beginDate || "";
this.params.endFollowupDate = query.endDate || "";
},
beforeDestroy() {
this.$bus.off("refrech");
},
methods: {
selectedUser(id) {
this.params.createUserId = id;
this.getData(1, 1);
},
dateQuery(date) {
this.params.beginFollowupDate = date.beginDate;
this.params.endFollowupDate = date.endDate;
this.getData(1, 1);
},
clearType() {
this.params.follState = 0;
this.params.custState = 0;
this.getData(1, 1);
},
selectedParams(item) {
switch (item.DictType) {
case 4:
this.params.follState = item.ID;
break;
case 5:
this.params.custState = item.ID;
break;
}
this.getData(1, 1);
},
query(item) {
if (this.leftType == 1) {
this.params.follState = item.ID;
} else {
this.params.custState = item.ID;
}
this.queryType = 0;
this.getData(1, 1);
},
getData(type, page) {
this.$vux.loading.show({
text: "加载中"
});

this.params.page = page || this.params.page;

this.getAxios("/Customer/GetMyCustomerFollowup", this.params)
.then(res => {
this.$vux.loading.hide();
if (res.Data.List.length > 0) {
if (type === 2) {
this.clientList = [...this.clientList, ...res.Data.List];
this.$refs.scroll.scroll.finishPullUp();
} else {
this.clientList = res.Data.List;
this.$refs.scroll.scrollTo(0, 0);
this.$refs.scroll.scroll.finishPullDown();
}
this.params.page += 1;
this.$refs.scroll.refresh();
} else if (this.params.page == 1) {
this.clientList = [];
this.$refs.scroll.forceUpdate();
} else {
this.$refs.scroll.forceUpdate();
}
})
.catch(err => {
this.$vux.loading.hide();
});
},
goto(path, id) {
this.$router.push({
path,
query: {
id
}
});
}
},
filters: {
getPicSrc(id) {
return `${apiPic}/dXNlci9sb2dv_${id}_35x35.png`;
},
dateFormat(date) {
return date ? dayjs(date).format("YYYY-MM-DD HH:mm") : "";
}
},
watch: {
clientList() {
setTimeout(() => {
this.$refs.scroll.forceUpdate(true);
}, 500);
}
},
components: {
Flexbox,
FlexboxItem,
XHeader,
Scroll,
NoData,
Group,
XButton,
Datetime,
Query
}
};



